.navbar {
    overflow: hidden;
    background-color: #333;  
    position: relative;
    /* bottom: 0; */
    width: 100%;
  }

  
  .navbar a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
  }
  
  .navbar a:hover {
    background-color: #ddd;
    color: black;
  }
  
  .navbar a.active {
    background-color: #4CAF50;
    color: white;
  }
  
  .navbar .icon {
    display: none;
  }
  
  @media screen and (max-width: 769px) {

    .navbar {
        bottom: 0 !important;
        position: fixed;
    }

    .navbar.responsive {
        position: fixed;
    }
    .navbar a:not(:first-child) {display: none;}
    .navbar a.icon {
      float: right;
      display: block;
    }
  }
  
  @media screen and (max-width: 769px) {
    .navbar.responsive a {
      float: none;
      display: block;
      text-align: left;
    }

    .mobile-hamburger-button {
        position: relative;
        float: right;
      }
  
  }
  